import { Injector } from '@angular/core';
import { HttpClient, HttpHandler, HttpXhrBackend, HttpHeaders } from '@angular/common/http';
import { Globalize } from 'wijmo/wijmo';
import { catchError } from 'rxjs/operators';
import { of } from 'rxjs';
import * as ace from 'ace-builds';
import { Interpreter } from 'pscript-interpreter';
import { mapTableToValues, parseDatetimeOrNull, dateToDtoString, replaceAll } from './utils';
import { TableDto } from './models';
import { Variables } from './app-variables';


const httpClient = getHttpClient();

export const mapFunction = new Map<string, (...args: any[]) => any>([
  ['mapTableToValues', mapTableDto],
  ['httpGet', (url: string, headers?: object) => httpClient.get<any>(url, getHttpOptions(headers)).pipe(
    catchError(e => of(e))
  ).toPromise()],
  ['httpPost', (url: string, body, headers?: object) => httpClient.post(url, body, getHttpOptions(headers)).pipe(
    catchError(e => of(e))
  ).toPromise()],
  ['httpPut', (url: string, body, headers?: object) => httpClient.put(url, body, getHttpOptions(headers)).pipe(
    catchError(e => of(e))
  ).toPromise()],
  ['httpDelete', (url: string, headers?: object) => httpClient.delete(url, getHttpOptions(headers)).pipe(
    catchError(e => of(e))
  ).toPromise()],
  ['dateTime', str => (str && str.length) ? parseDatetimeOrNull(str) || new Date() : new Date()],
  ['format', (value: Date | Number | string, displayFormat: string = null) => format(value, displayFormat)],
  ['getUrlQueryParams', () => {
    try {
      const search = location.search.slice(1);
      return JSON.parse('{"' + decodeURI(search).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') + '"}');
    } catch (e) {
      return {};
    }
  }]
]);

export function registerFunction(name: string, func: (...args: any[]) => any) {
  if (name && typeof func === 'function') {
    mapFunction.set(name, func);
  } else {
    throw Error(`registerFunction: incorrect parameters: ${name}, ${func}`);
  }
}

export function getInterpreter(): Interpreter {
  const interpreter = Interpreter.create();
  interpreter.assignGlobalContext({
    app: Variables.variables,
    session: Variables.contextVariables
  });
  return addFunctions(interpreter);
}

function addFunctions(interpreter: Interpreter): Interpreter {
  mapFunction.forEach((func, name) => {
    interpreter.addFunction(name, func);
  });
  interpreter.assignGlobalContext({
    ArrayUtils: {
      map(data: object[], props: Array<[string, string]>) {
        if (!data || !props) {
          return data;
        }

        return data.map(item => {
          return props.reduce((prev, [oldKey, newKey]) => {
            prev[newKey] = item[oldKey];
            return prev;
          }, {});
        });
      },
      toArray(val: any) {
        if (val && !(val instanceof Array)) {
          return [val];
        }
      },
      join(arr1: object[], arr2: object[], key1: string, key2?: string): object[] {
        if (!key2) {
          key2 = key1;
        }

        return arr1.map((item1) => {
          const i2 = arr2.find(item2 => item2[key2] === item1[key1]);
          return {...item1, ...i2};
        });

      }
    }
  });

  initEditorMode(interpreter);
  return interpreter;
}

let initializedMode = false;
function initEditorMode(interpreter: Interpreter) {
  if (initializedMode) { return; }
  const tools = ace.require('ace/ext/language_tools');
  const interpreterCompleter: ace.Ace.Completer = {
    getCompletions: (editor: ace.Ace.Editor, session: ace.Ace.EditSession, pos: ace.Ace.Point, prefix: string,
      callback: ace.Ace.CompleterCallback): void => {
      const line = editor.session.getLine(pos.row);
      const regexp = `([\\w.]+).${prefix}`;
      const res = line.match(new RegExp(regexp));
      const wordList = interpreter.getAutocompletionList(res ? res[1] : null) as ace.Ace.Completion[];
      callback(null, wordList);
    }
  };
  tools.addCompleter(interpreterCompleter);
  initializedMode = true;
}

function getHttpOptions(headers: object): object {
  if (headers && typeof headers === 'object') {
    return { headers: getHeaders(headers) };
  }
}

function getHeaders(headers: object) {
  if (headers && typeof headers === 'object') {
    let httpHeader = new HttpHeaders();

    Object.entries(headers).forEach(([name, value]) => {
      httpHeader = httpHeader.set(name, value as string);
    });

    return httpHeader;
  }
}

function format(value: Date | Number | string, displayFormat: string | object = null): string {
  if (!value) { return ''; }

  if (typeof value === 'string') {
    return displayFormat ? templateString(String(value), displayFormat as object) : value;
  }

  if (value instanceof Date) {
    const dt = value as Date;
    return (!displayFormat) ? dateToDtoString(dt) : Globalize.format(dt, String(displayFormat));
  }

  if (typeof value === 'object') {
    return JSON.stringify(value);
  }

  if (typeof value === 'number') {
    const num = +value;
    return (!displayFormat) ? num.toLocaleString() : Globalize.formatNumber(num, String(displayFormat));
  }
  return String(value);
}

function templateString(formatTemplate: string, values: object): string {
  const keys = Object.keys(values);
  let result = formatTemplate;
  for (const key of keys) {
    if (key && !key.startsWith('_')) {
      result = replaceAll(key, values[key]);
    }
  }
  return result;
}

function mapTableDto(table: TableDto, fields?) {
  if (fields) {
    table.dataTypes = table.fieldNames.map(fieldName => {
      const field = fields.find(f => f.fieldName === fieldName);
      if (field) {
        return field.dataType;
      }
    });
  }
  return mapTableToValues(table);
}

function getHttpClient(): HttpClient {
  const injector = Injector.create({
    providers: [
      { provide: HttpClient, deps: [HttpHandler] },
      { provide: HttpHandler, useValue: new HttpXhrBackend({ build: () => new XMLHttpRequest }) },
    ],
  });
  return injector.get(HttpClient);
}
