import { Injectable } from '@angular/core';
import { SqlDbService } from './sql-db.service';
import { NotificationType } from '../models';
import { NotificationService } from './notification.service';
import { AuthService } from 'src/app/security';
import { registerFunction } from '../interpreter';

@Injectable({
  providedIn: 'root'
})
export class InterpreterService {
  constructor(
    private notificationService: NotificationService,
    private authService: AuthService) {

      registerFunction('notify', (message: string, type = NotificationType.SUCCESS) => {
        notificationService.open({type, message});
      });

      registerFunction('alert', (message: string) => {
        notificationService.open({type: NotificationType.DANGER, message});
      });

      registerFunction('authorizeWithAccessCode', async(publicAccessCode: string, saveAs?) => {
        return await authService.authorize(publicAccessCode, saveAs).toPromise();
      });
  }
}
